body, html {
  width: 100%;
  height: 100%;
}
.wraps {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
.wraps .wrap {
  width: 1080px;
  height: 100%;
  margin: 0 auto;
  margin-top: .2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid #ccc;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 0 .1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.wraps header {
  width: 100%;
  height: .5rem;
}
.wraps section {
  width: 100%;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 2px dashed rgba(0, 0, 0, 0.12);
  margin-bottom: .4rem;
}
.wraps section .left {
  width: 75%;
  height: 100%;
  border-left: 2px dashed rgba(0, 0, 0, 0.12);
  border-right: 2px dashed rgba(0, 0, 0, 0.12);
  padding: 0 .1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: auto;
}
.wraps section .left .left-box {
  border-top: 3px solid rgba(0, 0, 0, 0.12);
}
.wraps section .left .left-box .left-title {
  width: 100%;
  height: .5rem;
  border-bottom: 1px solid #ccc;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 .2rem;
}
.wraps section .left .left-box .left-title h5 {
  line-height: .5rem;
  font-weight: 500;
  color: #666A6C;
}
.wraps section .left .left-box .left-title .left-btn button {
  border: none;
  outline: none;
  padding: .05rem;
  border-radius: .02rem;
}
.wraps section .left .left-box .left-title .left-btn .btn-warning {
  background: #FAAD58;
  color: #fff;
}
.wraps section .left .left-content {
  width: 100%;
  height: auto;
}
.wraps section .right {
  width: 25%;
  height: 100%;
  border-right: 2px dashed rgba(0, 0, 0, 0.12);
  padding-right: 15px;
  padding-left: 15px;
}
.wraps section .right .right-box {
  border-top: 3px solid rgba(0, 0, 0, 0.12);
}
.wraps section .right .right-box .right-title {
  width: 100%;
  height: .4rem;
  border-bottom: 1px solid #ccc;
  padding-left: .2rem;
}
.wraps section .right .right-box .right-title h5 {
  line-height: .4rem;
  font-weight: 500;
  color: #666A6C;
}
.wraps section .right .right-box .right-content {
  width: 100%;
  height: auto;
}
.wraps section .right .right-box .right-content .form-horizontal {
  width: 100%;
  height: auto;
}
.wraps section .right .right-box .right-content .form-horizontal p {
  line-height: .4rem;
}
.wraps section .right .right-box .right-content .form-horizontal p b {
  font-family: '\5FAE\8F6F\96C5\9ED1';
  color: #999;
  font-size: .13rem;
}
.wraps section .right .right-box .right-content .form-horizontal table {
  width: 100%;
}
.wraps section .right .right-box .right-content .form-horizontal table tr {
  width: 100%;
}
.wraps section .right .right-box .right-content .form-horizontal table tr td {
  width: 28%;
  height: .4rem;
  display: inline-block;
  line-height: .4rem;
  background: #EDF1F5;
  text-align: center;
  margin: .05rem;
  font-size: .12rem;
  color: #666A6C;
}
.wraps section .right .right-box .right-content .form-horizontal .form-group {
  width: 100%;
  height: .33rem;
  background: #EDF1F5;
  margin: .1rem 0;
  padding: 0 .2rem;
}
.wraps section .right .right-box .right-content .form-horizontal .form-group .label {
  vertical-align: middle;
  line-height: .33rem;
  color: #666A6C;
}
.wraps section .right .right-box .right-content .form-horizontal .form-group .label i {
  color: #666A6C;
}
.wraps section .right .right-box .right-content .form-horizontal .form-group .label img {
  vertical-align: middle;
  float: right;
}
.wraps .wrap section .newleft {
  width: 150%;
}
